import { useContext, useState } from 'react'

import { css } from '@linaria/core'

import { MarksContext } from 'Context'

import VerticalLayout from 'Components/Layouts/VerticalLayout'
import SubjectMarks from 'Activities/Marks/QuarterMarks/SubjectMarks'
import SheetView from '../../../../Components/SheetView'
import InputModal from '../../../../Components/InputModal'
import SubjectName from './SubjectName'
import Tools from './Tools'
import Link from './Link'
import LastChange from './LastChange'

const styles = css`
    display: flex;
    flex-direction: column;
    gap: 8px;
    padding: 14px;

    background-color: var(--bg3);
    border-radius: 9px;
    box-shadow: 0 0 0 1px var(--lvl4-borders) inset;
    position: relative;

    .marks-list {
        --bg2: var(--bg3);
        margin: -4px;

        .main-content {
            padding: 0;
            padding-right: 40px;
        }
        .SubjectMarks {
            min-height: 78px;
        }
    }

    .homework-view {
        font-weight: 500;
        font-size: 18px;
        -webkit-user-select: all;
        user-select: all;
        word-break: break-word;
    }

    .link-wraper {
        position: absolute;
        right: 6px;
        top: 6px;
    }
`

const InfoBlock = ({ children }) => {
    return <div className={styles}>{children}</div>
}

const dstyles = css`
    font-size: 14px;
    padding-left: 14px;
    margin-bottom: -3px;
    color: var(--text2);
`

const Title = ({ children }) => {
    return <div className={dstyles}>{children}</div>
}

const LessonInfo = ({ lesson, path, subject, handleClose }) => {
    const [isEditing, setIsEditing] = useState(false)

    const marksAll = useContext(MarksContext)
    const marks = marksAll?.[lesson.id] ?? []
    const markTarget = marksAll['marksTargets']?.[lesson.id]

    return (
        <SheetView handleClose={handleClose}>
            <VerticalLayout>
                <SubjectName subject={subject} />
                <Title>Домашнее задание</Title>
                <InfoBlock>
                    <div
                        className="homework-view"
                        onClick={() => setIsEditing(true)}
                    >
                        {lesson.hw}
                    </div>
                    <LastChange lesson={lesson} />
                    <Tools homework={lesson.hw} />
                </InfoBlock>
                {lesson.link && (
                    <>
                        <Title>Прикрепленная ссылка</Title>
                        <Link url={lesson.link} />{' '}
                    </>
                )}
                {subject.marks && (
                    <>
                        <Title>Мои оценки</Title>
                        <InfoBlock>
                            <div className="marks-list">
                                <SubjectMarks
                                    marks={marks}
                                    subject={subject}
                                    embedded
                                    target={markTarget}
                                />
                            </div>
                        </InfoBlock>
                    </>
                )}
                {isEditing && (
                    <InputModal
                        handleClose={() => setIsEditing(false)}
                        path={path}
                        lesson={lesson}
                    />
                )}
            </VerticalLayout>
        </SheetView>
    )
}

export default LessonInfo
